import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, Pressable, TextInput, ScrollView, TouchableOpacity, Alert, Platform } from 'react-native';
import { theme } from './colors';
import { useEffect, useState } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import {Fontisto} from "@expo/vector-icons"

const STORAGE_KEY = "@todos"
const LOCATION = "@loc"

export default function App() {
  const [working, setWorking] = useState(true);
  const [text, setText] = useState("");
  const [modText, setModText] = useState("");
  const [todos, setTodos] = useState({});
  const [loading, setLoading] = useState(true);
  const [modify, setModify] = useState("");
  const habit = async () => {
    setWorking(false);
    await AsyncStorage.setItem(LOCATION, "habit")
  };
  const work = async () => {
    setWorking(true);
    await AsyncStorage.setItem(LOCATION, "work")
  };
  const onChangeText = (payload) => {
    setText(payload)
  }
  const onChangeModify = (payload) => {
    setModText(payload)
  } 
  const saveTodos = async (toSave) => {
    const data = JSON.stringify(toSave)
    await AsyncStorage.setItem(STORAGE_KEY, data);
  }
  const loadTodos = async () => {
    try {
      const s = await AsyncStorage.getItem(STORAGE_KEY);
      setTodos(JSON.parse(s));
    } catch (error) {
      console.log(error)
    }
  }
  const loadLocation = async () => {
    const location = await AsyncStorage.getItem(LOCATION);
    if (location === "habit") {
      habit();
    }
  }
  useEffect(()=> {
    loadLocation();
    loadTodos();
  }, [])
  const addTodo = async () => {
    if (text === "") {
      return;
    }
    const newTodos = {...todos, [Date.now()]: {text, working, complete: false}}
    setTodos(newTodos);
    await saveTodos(newTodos);
    setText("")
  }
  const completeTodo = async (key) => {
    const newTodos = {...todos}
    newTodos[key].complete = !newTodos[key].complete;
    setTodos(newTodos);
    await saveTodos(newTodos);
  }
  const modifyTodo = async (key) => {
    const newTodos = {...todos}
    newTodos[key].text = modText
    setTodos(newTodos);
    await saveTodos(newTodos);
    setModify("")
  }
  const deleteTodo = async (key) => {
    if (Platform.OS === "web") {
      const ok = confirm("할 일을 지우겠습니까?");
      if (ok) {
        const newTodos = {...todos};
        delete newTodos[key];
        setTodos(newTodos);
        await saveTodos(newTodos);
      }
    } else {
      Alert.alert("할 일 지우기", "지우겠습니까?", [
        {text: "Cancel"}, {text: "I'm Sure", onPress: async () => {
          const newTodos = {...todos};
          delete newTodos[key];
          setTodos(newTodos);
          await saveTodos(newTodos);
        }}
      ]);
    }
  }
  return (
    <View style={styles.container}>
      <StatusBar style="light" />
      <View style={styles.header}>
        <Pressable 
          onPress={work}
        >
          <Text style={{...styles.btnText, color: working ? "white": theme.grey}}>work</Text>
        </Pressable>
        <Pressable  onPress={habit}>
        <Text style={{...styles.btnText, color: !working ? "white": theme.grey}}>habit</Text>
        </Pressable>
      </View>
      <View>
        <TextInput
          onSubmitEditing={addTodo}
          onChangeText={onChangeText}
          returnKeyType='done'
          value={text}
          style={styles.input} 
          placeholder={working ? "할 일을 입력하세요." : "당신의 오늘 습관은?"}></TextInput>
      </View>
      <ScrollView>
        {todos !== undefined && todos !== null ? Object.keys(todos).map(key => 
          todos[key].working === working ?
          ( 
            key === modify ? (
            <View key={key} style={styles.todo}>
              <TextInput style={styles.modInput} autoFocus defaultValue={todos[key].text} onChangeText={onChangeModify}></TextInput>
              <TouchableOpacity onPress={()=>modifyTodo(key)}>
                <Fontisto name="check" size={18} color={theme.grey}></Fontisto>
              </TouchableOpacity>
            </View>
            ) : 
            (
              <View key={key} style={styles.todo}>{
                (todos[key].complete ? (
                    <View style={styles.checkbox}>
                      <TouchableOpacity onPress={()=> completeTodo(key)}>
                        <Fontisto name="checkbox-active" size={18} color={theme.grey}></Fontisto>
                      </TouchableOpacity>
                    </View>): (
                    <View style={styles.checkbox}>
                      <TouchableOpacity onPress={()=> completeTodo(key)}>
                        <Fontisto name="checkbox-passive" size={18} color={theme.grey}></Fontisto>
                      </TouchableOpacity>
                    </View>
                  )
                )
              }
                <Text style={{...styles.todoText, textDecorationLine: todos[key].complete ? "line-through" : "none", fontWeight: todos[key].complete ? "300" : "500"}}>{todos[key].text}</Text>
                <View style={styles.modBtn}>
                  <TouchableOpacity onPress={()=> setModify(key)}><Fontisto name="eraser" size={18} color={theme.grey}></Fontisto></TouchableOpacity>
                  <TouchableOpacity onPress={()=> deleteTodo(key)}><Fontisto name="trash" size={18} color={theme.grey}></Fontisto></TouchableOpacity>
                </View>
              </View> 
          ))
        : null
        ) : null}
      </ScrollView>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: theme.bg,
    paddingHorizontal: 10,
  },
  header: {
    justifyContent: "space-between",
    flexDirection: "row",
    marginTop: 50,
  },
  btnText: {
    fontSize: 38,
    fontWeight: "600",
    color: "white",
  },
  input: {
    backgroundColor:"white",
    paddingVertical: 15,
    paddingHorizontal: 20,
    borderRadius: 30,
    marginVertical: 20,
    fontSize: 15
  },
  checkbox: {
    flex: 0.7,
    color: "white"
  },
  todo: {
    backgroundColor: theme.todoBg,
    marginBottom: 10,
    paddingVertical: 25,
    paddingHorizontal: 25,
    borderRadius: 15,
    flexDirection: "row",
    justifyContent:'space-between'
  },
  todoText: {
    flex: 5,
    color: "white",
    fontSize: 16,
    fontWeight: "500"
  },
  modBtn: {
    flex: 1,
    flexDirection: "row",
    justifyContent: "space-between",
  },
  modInput: {
    color: "white",
    fontSize: 16,
    backgroundColor: theme.grey,
  }
});
